.title {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  font-size: 60px;
  line-height: 90px;
  color: #e0e0e0;
}

.paragraph {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;

  color: #e0e0e0;
}

@media (max-width: 650px) {
  .title {
    font-size: 48px;
    line-height: 0px;
  }
}
