.page {
  width: 100vw;
  height: 100vh;
}

.Container {
  display: flex;
  flex-direction: row;
  margin-top: 10%;
}

.LeftContainer {
  width: 33rem;
  height: 28rem;
}

.RightContainer {
  display: flex;
  flex-direction: column;
  font-style: normal;
  letter-spacing: -0.035em;
}

.RightContainer h1 {
  font-weight: 700;
  font-size: 64px;
  line-height: 95px;
  color: #333333;
  font-family: 'Space Mono' !important;
}

.RightContainer p {
  font-weight: 400;
  font-size: 24px;
  line-height: 36px;
  color: #4f4f4f;
  font-family: 'Space Mono' !important;
}

.RightContainer button {
  font-family: 'Space Mono' !important;
  font-size: 14px;
  font-weight: 700;
  line-height: 21px;
  letter-spacing: -0.035em;

  width: 13rem;
  height: 4.25rem;

  background-color: #333333;
  color: #ffffff;

  margin-top: 50px;

}

@media (max-width: 650px) {
  .Container {
    flex-direction: column;
    justify-content: center;
  }

  .LeftContainer {
    width: 80%;
    height: 80%;
    margin-left: 10%;
  }

  .RightContainer h1 {
    font-size: 48px;
    line-height: 1;
    margin-top: 40px;
  }

  .RightContainer p {
    font-size: 18px;
    margin-top: 0px;
  }

  .RightContainer button {
    justify-content: center;
    margin-top: 30px;
  }
}

.title {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  font-size: 60px;
  line-height: 90px;
  color: #e0e0e0;
}

.paragraph {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;

  color: #e0e0e0;
}

@media (max-width: 650px) {
  .title {
    font-size: 48px;
    line-height: 0px;
  }
}

.form-title {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  line-height: 45px;
  margin-top: 90px;
  margin-bottom: 25px;
  color: #e0e0e0;
}
.input-container {
  display: flex;
  flex-direction: column;
  padding-bottom: 70px;
}

.form-input {
  width: 300px;
  height: 60px;
  margin-bottom: 33px;
  padding-left: 10px;

  background: #26292c;
  border: 1px solid #ffffff;
  border-radius: 50px;

  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;

  color: #e0e0e0;
}

.form-button {
  width: 310px;
  height: 67px;
  background: linear-gradient(90deg, #ff2d04 0%, #c13216 100%);
  box-shadow: inset 5px 5px 15px rgba(0, 0, 0, 0.15);
  border-radius: 50px;

  font-family: 'Poppins';
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 27px;

  color: #ffffff;
}

.login-error {
  font-family: 'Poppins' !important;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  text-align: center;
  color: #e9b425;
  margin-right: auto;
  margin-left: 20px;
  width: 270px;
}

.login-error-border {
  width: 300px;
  height: 60px;
  margin-bottom: 33px;
  padding-left: 10px;

  background: #26292c;
  border: 1px solid #e9b425;
  border-radius: 50px;

  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;

  color: #e0e0e0;
}

@media (max-width: 650px) {
  .form-button {
    width: 270px;
  }
  .form-input {
    width: 250px;
    height: 50px;
    margin-bottom: 20px;
    padding-left: 10px;
  }

  .login-error {
    margin-right: 25px;
    width: 220px;
  }

  .login-error-border {
    width: 250px;
    height: 50px;
    margin-bottom: 20px;
    padding-left: 10px;
  }

  .input-container {
    padding-bottom: 40px;
  }

  .form-title {
    margin-top: 50px;
    margin-bottom: 25px;
  }
}

.header-title {
  font-family: 'Inconsolata' !important;
  font-size: 24px;
  font-weight: 700;
  line-height: 25px;
  letter-spacing: -0.08em;
  text-align: left;

  color: #333333;
}

.FooterText {
  font-family: 'Montserrat' !important;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #bdbdbd;

  text-align: center;
  position: fixed;
  bottom: 0;
  left: 0;
  height: 20px;
  width: 100%;
}

.FooterLink,
a:link,
a:active,
a:hover,
a:visited {
  font-weight: 700;
  color: #bdbdbd;
}

.container-1 {
  display: flex;
  flex-direction: row;
  background: linear-gradient(180deg, #33383d 0%, #1c1d20 100%);
  box-shadow: 4px 4px 70px rgba(0, 0, 0, 0.25);
  height: 100vh;
  width: 100vw;
}

.flex-item-1 {
  display: flex;
  flex-direction: column;
  width: 50vw;
  margin-left: 15%;
  margin-top: 8%;
}

.flex-item-2 {
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.background-image-1 {
  width: 50vw;
  height: 100vh;
}

.compasso-logo-1 {
  position: absolute;
  margin-top: 25px;
}

@media (max-width: 650px) {
  .background-image-1 {
    display: none;
  }

  .compasso-logo-1 {
    width: 280px;
    height: 80px;
    margin-top: 0;
    right: 10%;
    left: 10%;
    padding-top: 0;
  }

  .flex-item-1 {
    margin-top: 35%;
    width: 80vw;
  }
}

.home-container {
  width: 100vw;
  height: 100vh;
}

.header-item-1 {
  height: 163px;
  width: 95vw;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.compasso-logo-2 {
  width: 300px;
  height: 84px;
  top: 19px;
  left: 28px;
}
.body-container-2 {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;

  min-height: 60vh;

  padding-top: 60px;
}

.background-image-2 {
  position: absolute;
  width: 60rem;
  max-height: 40rem;
  left: -160px;
  top: 300px;
  margin-top: 90px;
  z-index: -1;
  -o-object-fit: scale-down;
     object-fit: scale-down;
}

.text-container {
  margin-right: 5%;
  text-align: right;
}

.text-container h1 {
  font-weight: 700;
  font-size: 48px;
  color: #c12d18;
  line-height: 0.8;
}
.text-title {
  font-size: 36px;
}
.text-container p {
  font-weight: 400;
  font-size: 16px;
  color: #222222;
}

.footer-container {
  position: fixed;
  width: 100%;
  height: 100px;

  background: linear-gradient(90.16deg, #33383d 0%, #1c1d20 100%);

  bottom: 0px;
  left: 0px;
  right: 0px;
  margin-bottom: 0px;
  z-index: 4;
}

.footer-content {
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.footer-text {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50%;

  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  text-align: right;
}

.footer-refresh-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 20%;
  height: 100px;
}

.refresh-text {
  width: 50%;
  text-align: center;
  color: white;
}

.refresh-time {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 50%;
  height: 100%;
  color: white;
}

.navigate {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 100px;
  height: 100px;

  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  text-align: center;

  color: #c13216;
  background-color: white;
}

.logout {
  width: 100px;
  height: 100px;

  color: #ffffff;
}

.logout-button {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 18px;
}

.weather-api {
  width: 120px;

  display: flex;
  flex-direction: column;
  justify-content: space-evenly;

  font-family: 'Poppins' !important;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  text-align: center;
  color: #222222;
  margin-top: 20px;
}

.temperature {
  display: flex;
  flex-direction: row;
  margin: auto;
  margin-top: 5px;
  font-weight: 700;
  font-size: 32px;
  text-align: center;
  color: #222222;
  grid-gap: 5px;
  gap: 5px;
}

@media (max-width: 650px) {
  .background-image-2 {
    position: fixed;
    width: 31.25rem;
    left: -25%;
    top: 35%;
    margin-top: 0px;
    max-height: 43rem;
    -o-object-fit: scale-down;
       object-fit: scale-down;
    -o-object-position: -4rem 5rem;
       object-position: -4rem 5rem;
  }

  .text-container {
    display: none;
  }

  .compasso-logo-2 {
    width: 250px;
    height: 72px;
    margin: 0;
    top: 0;
    left: 0;
  }

  .footer-text {
    display: none;
  }
  .footer-refresh-container {
    display: none;
  }

  .footer-content {
    flex-direction: row-reverse;
  }

  .navigate {
    width: 100%;
  }

  .logout {
    width: 40%;
  }
}

